import { Command } from 'commander';
import prompts from 'prompts';
import chalk from 'chalk';
import ora from 'ora';

interface ChatMessage {
  role: 'user' | 'assistant';
  content: string;
  timestamp: Date;
}

interface ChatSession {
  messages: ChatMessage[];
  sessionId: string;
  startTime: Date;
}

export default function naturalChatCommand(program: Command) {
  program
    .command('chat')
    .description('Ëá™ÁÑ∂Ë®ÄË™û„Åß„ÅÆÂØæË©±Âûã„ÉÅ„É£„ÉÉ„Éà„Çª„ÉÉ„Ç∑„Éß„É≥')
    .option('-m, --mode <mode>', 'Âãï‰Ωú„É¢„Éº„Éâ (chat/research/creative)', 'chat')
    .option('-v, --verbose', 'Ë©≥Á¥∞Âá∫Âäõ„ÇíË°®Á§∫', false)
    .action(async (options) => {
      console.log(chalk.cyan.bold('ü§ñ MARIA CODE Chat Interface'));
      console.log(chalk.gray(`Mode: ${options.mode} | Verbose: ${options.verbose}`));
      console.log(chalk.yellow('Type your request in natural language. Type "exit" to quit.\n'));

      const session: ChatSession = {
        messages: [],
        sessionId: generateSessionId(),
        startTime: new Date()
      };

      // „ÉÅ„É£„ÉÉ„Éà„É´„Éº„Éó
      while (true) {
        try {
          const response = await prompts({
            type: 'text',
            name: 'message',
            message: chalk.blue('You:'),
            validate: (value) => value.length > 0 ? true : 'Please enter a message'
          });

          if (!response.message) {
            console.log(chalk.yellow('üëã Chat session ended.'));
            break;
          }

          const userMessage = response.message.trim();
          
          // ÁµÇ‰∫Ü„Ç≥„Éû„É≥„Éâ
          if (userMessage.toLowerCase() === 'exit' || userMessage.toLowerCase() === 'quit') {
            console.log(chalk.yellow('üëã Chat session ended.'));
            break;
          }

          // „Éò„É´„Éó„Ç≥„Éû„É≥„Éâ
          if (userMessage.toLowerCase() === 'help') {
            showHelp();
            continue;
          }

          // Â±•Ê≠¥„Ç≥„Éû„É≥„Éâ
          if (userMessage.toLowerCase() === 'history') {
            showHistory(session);
            continue;
          }

          // „ÇØ„É™„Ç¢„Ç≥„Éû„É≥„Éâ
          if (userMessage.toLowerCase() === 'clear') {
            console.clear();
            console.log(chalk.cyan.bold('ü§ñ MARIA CODE Chat Interface'));
            console.log(chalk.gray('Chat history cleared.\n'));
            session.messages = [];
            continue;
          }

          // „É¶„Éº„Ç∂„Éº„É°„ÉÉ„Çª„Éº„Ç∏„ÇíË®òÈå≤
          session.messages.push({
            role: 'user',
            content: userMessage,
            timestamp: new Date()
          });

          // AIÂøúÁ≠î„Çí„Ç∑„Éü„É•„É¨„Éº„Éà
          const aiResponse = await processUserMessage(userMessage);
          
          // AIÂøúÁ≠î„ÇíË®òÈå≤
          session.messages.push({
            role: 'assistant',
            content: aiResponse,
            timestamp: new Date()
          });

          console.log(chalk.green('ü§ñ MARIA:'), aiResponse);
          console.log(''); // Á©∫Ë°å

        } catch (error) {
          if (error instanceof Error && error.message.includes('cancelled')) {
            console.log(chalk.yellow('\nüëã Chat session cancelled.'));
            break;
          }
          console.error(chalk.red('Error:'), error);
        }
      }

      // „Çª„ÉÉ„Ç∑„Éß„É≥ÁµÇ‰∫ÜÊôÇ„ÅÆÁµ±Ë®à
      showSessionStats(session);
    });
}

async function processUserMessage(
  message: string
): Promise<string> {
  const spinner = ora('ü§î MARIA is thinking...').start();
  
  try {
    // „Ç∑„Éü„É•„É¨„Éº„Ç∑„Éß„É≥ÂæÖÊ©ü
    await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 2000));
    
    spinner.stop();

    // Ëá™ÁÑ∂Ë®ÄË™ûÂá¶ÁêÜ„ÅÆ„Ç∑„Éü„É•„É¨„Éº„Ç∑„Éß„É≥
    const response = await generateResponse(message);
    return response;
    
  } catch (error) {
    spinner.stop();
    return `Áî≥„ÅóË®≥„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇ„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error instanceof Error ? error.message : 'Unknown error'}`;
  }
}

async function generateResponse(
  message: string
): Promise<string> {
  const lowerMessage = message.toLowerCase();
  
  // „Éë„Çø„Éº„É≥„Éû„ÉÉ„ÉÅ„É≥„Ç∞„Å´„Çà„ÇãÂøúÁ≠îÁîüÊàê
  if (lowerMessage.includes('create') || lowerMessage.includes('‰ΩúÊàê') || lowerMessage.includes('‰Ωú„Å£„Å¶')) {
    if (lowerMessage.includes('file') || lowerMessage.includes('„Éï„Ç°„Ç§„É´')) {
      return `„Éï„Ç°„Ç§„É´‰ΩúÊàê„ÅÆ„Åî‰æùÈ†º„Åß„Åô„Å≠„ÄÇ‰ª•‰∏ã„ÅÆÊâãÈ†Ü„ÅßÈÄ≤„ÇÅ„Åæ„ÅôÔºö\n\n1. „Éï„Ç°„Ç§„É´Âêç„Å®ÂΩ¢Âºè„ÇíÁ¢∫Ë™ç\n2. ÂøÖË¶Å„Å™ÂÜÖÂÆπ„ÇíÊï¥ÁêÜ\n3. „Éï„Ç°„Ç§„É´‰ΩúÊàê„Å®‰øùÂ≠ò\n\nÂÖ∑‰ΩìÁöÑ„Å™„Éï„Ç°„Ç§„É´Âêç„Å®ÂÜÖÂÆπ„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`;
    }
    if (lowerMessage.includes('function') || lowerMessage.includes('Èñ¢Êï∞')) {
      return `Èñ¢Êï∞‰ΩúÊàê„ÅÆ„É™„ÇØ„Ç®„Çπ„Éà„ÇíÊâø„Çä„Åæ„Åó„Åü„ÄÇ\n\nÂøÖË¶Å„Å™ÊÉÖÂ†±Ôºö\n‚Ä¢ Èñ¢Êï∞Âêç\n‚Ä¢ ÂºïÊï∞„ÅÆÂûã„Å®ÂêçÂâç\n‚Ä¢ Êàª„ÇäÂÄ§„ÅÆÂûã\n‚Ä¢ Âá¶ÁêÜÂÜÖÂÆπ\n\n„Åì„Çå„Çâ„ÅÆË©≥Á¥∞„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇTypeScript/JavaScript„ÅßÂÆüË£Ö„Åó„Åæ„Åô„ÄÇ`;
    }
    if (lowerMessage.includes('component') || lowerMessage.includes('„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà')) {
      return `React„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÅÆ‰ΩúÊàê„Åß„Åô„Å≠ÔºÅ\n\n‰ª•‰∏ã„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑÔºö\n‚Ä¢ „Ç≥„É≥„Éù„Éº„Éç„É≥„ÉàÂêç\n‚Ä¢ ÂøÖË¶Å„Å™„Éó„É≠„Éë„ÉÜ„Ç£\n‚Ä¢ Ë¶ã„ÅüÁõÆ„ÇÑÊ©üËÉΩ„ÅÆË¶Å‰ª∂\n‚Ä¢ „Çπ„Çø„Ç§„É™„É≥„Ç∞ÊñπÊ≥ïÔºàTailwind CSS‰ΩøÁî®ÂèØËÉΩÔºâ`;
    }
  }

  if (lowerMessage.includes('fix') || lowerMessage.includes('‰øÆÊ≠£') || lowerMessage.includes('Áõ¥„Åó„Å¶')) {
    return `ÂïèÈ°å„ÅÆ‰øÆÊ≠£„Çí„ÅäÊâã‰ºù„ÅÑ„Åó„Åæ„Åô„ÄÇ\n\nË©≥Á¥∞ÊÉÖÂ†±„Çí„ÅäËÅû„Åã„Åõ„Åè„Å†„Åï„ÅÑÔºö\n‚Ä¢ „Å©„ÅÆ„Çà„ÅÜ„Å™ÂïèÈ°å„ÅåÁô∫Áîü„Åó„Å¶„ÅÑ„Åæ„Åô„ÅãÔºü\n‚Ä¢ „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏„ÅØ„ÅÇ„Çä„Åæ„Åô„ÅãÔºü\n‚Ä¢ ÂïèÈ°å„ÅåËµ∑„Åç„Å¶„ÅÑ„Çã„Éï„Ç°„Ç§„É´„ÇÑ„Ç≥„Éº„Éâ„ÅØ„ÅÇ„Çä„Åæ„Åô„ÅãÔºü\n\nÊÉÖÂ†±„Çí„ÅÑ„Åü„Å†„Åë„Çå„Å∞„ÄÅÈÅ©Âàá„Å™Ëß£Ê±∫Á≠ñ„ÇíÊèêÊ°à„Åó„Åæ„Åô„ÄÇ`;
  }

  if (lowerMessage.includes('explain') || lowerMessage.includes('Ë™¨Êòé') || lowerMessage.includes('Êïô„Åà„Å¶')) {
    return `Ë™¨Êòé„ÅÆ„Åî‰æùÈ†º„Åß„Åô„Å≠„ÄÇ‰ª•‰∏ã„ÅÆ„Çà„ÅÜ„Å™ÂÜÖÂÆπ„Å´„Å§„ÅÑ„Å¶Ë©≥„Åó„ÅèË™¨Êòé„Åß„Åç„Åæ„ÅôÔºö\n\n‚Ä¢ „Ç≥„Éº„Éâ„ÅÆÂãï‰ΩúÂéüÁêÜ\n‚Ä¢ „É©„Ç§„Éñ„É©„É™„ÇÑ„Éï„É¨„Éº„É†„ÉØ„Éº„ÇØ„ÅÆ‰Ωø„ÅÑÊñπ\n‚Ä¢ „Éô„Çπ„Éà„Éó„É©„ÇØ„ÉÜ„Ç£„Çπ\n‚Ä¢ „Ç¢„Éº„Ç≠„ÉÜ„ÇØ„ÉÅ„É£„ÅÆË®≠Ë®à\n\nÂÖ∑‰ΩìÁöÑ„Å´‰Ωï„Å´„Å§„ÅÑ„Å¶Áü•„Çä„Åü„ÅÑ„ÅãÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`;
  }

  if (lowerMessage.includes('test') || lowerMessage.includes('„ÉÜ„Çπ„Éà')) {
    return `„ÉÜ„Çπ„Éà‰ΩúÊàê„ÅÆ„Çµ„Éù„Éº„Éà„Çí„Åó„Åæ„Åô„ÄÇ\n\n„ÉÜ„Çπ„Éà„ÅÆÁ®ÆÈ°ûÔºö\n‚Ä¢ „É¶„Éã„ÉÉ„Éà„ÉÜ„Çπ„ÉàÔºàÈñ¢Êï∞„Éª„Ç≥„É≥„Éù„Éº„Éç„É≥„ÉàÂçò‰ΩçÔºâ\n‚Ä¢ Áµ±Âêà„ÉÜ„Çπ„ÉàÔºàÊ©üËÉΩÂçò‰ΩçÔºâ\n‚Ä¢ E2E„ÉÜ„Çπ„ÉàÔºà„Ç∑„Éä„É™„Ç™„Éô„Éº„ÇπÔºâ\n\n„Å©„ÅÆ„Çà„ÅÜ„Å™„ÉÜ„Çπ„Éà„Çí‰ΩúÊàê„Åó„Åü„ÅÑ„ÅãË©≥Á¥∞„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`;
  }

  if (lowerMessage.includes('deploy') || lowerMessage.includes('„Éá„Éó„É≠„Ç§')) {
    return `„Éá„Éó„É≠„Ç§„É°„É≥„Éà„Å´„Å§„ÅÑ„Å¶„Çµ„Éù„Éº„Éà„Åó„Åæ„Åô„ÄÇ\n\nÂà©Áî®ÂèØËÉΩ„Å™Áí∞Â¢ÉÔºö\n‚Ä¢ Development (dev)\n‚Ä¢ Staging (stg)\n‚Ä¢ Production (prod)\n\nÁèæÂú®„ÅÆË®≠ÂÆö„ÇÑ„Éá„Éó„É≠„Ç§„Åó„Åü„ÅÑÁí∞Â¢É„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`;
  }

  // „Éó„É≠„Ç∏„Çß„ÇØ„ÉàÈñ¢ÈÄ£
  if (lowerMessage.includes('project') || lowerMessage.includes('„Éó„É≠„Ç∏„Çß„ÇØ„Éà')) {
    return `„Éó„É≠„Ç∏„Çß„ÇØ„Éà„Å´Èñ¢„Åô„Çã„ÅîË≥™Âïè„Åß„Åô„Å≠„ÄÇ\n\nMARIA PLATFORM„ÅÆ‰∏ªË¶ÅÊ©üËÉΩÔºö\n‚Ä¢ Paper Editor - LaTeXË´ñÊñáÁ∑®ÈõÜ\n‚Ä¢ Slides Editor - „Éó„É¨„Çº„É≥„ÉÜ„Éº„Ç∑„Éß„É≥‰ΩúÊàê\n‚Ä¢ AI Chat - ÂØæË©±ÂûãÈñãÁô∫ÊîØÊè¥\n‚Ä¢ Graph RAG - Knowledge Graph (optional)\n\n‰Ωï„ÅãÁâπÂÆö„ÅÆÊ©üËÉΩ„Å´„Å§„ÅÑ„Å¶Ë©≥„Åó„ÅèÁü•„Çä„Åü„ÅÑ„Åì„Å®„ÅØ„ÅÇ„Çä„Åæ„Åô„ÅãÔºü`;
  }

  // ‰∏ÄËà¨ÁöÑ„Å™ÂøúÁ≠î
  const responses = [
    `„Å™„Çã„Åª„Å©„ÄÅ„Äå${message}„Äç„Å´„Å§„ÅÑ„Å¶„Åß„Åô„Å≠„ÄÇ„ÇÇ„ÅÜÂ∞ë„ÅóË©≥Á¥∞„ÇíÊïô„Åà„Å¶„ÅÑ„Åü„Å†„Åë„Åæ„Åô„ÅãÔºüÂÖ∑‰ΩìÁöÑ„Å´„Å©„ÅÆ„Çà„ÅÜ„Å™‰ΩúÊ•≠„Çí„ÅäÊâã‰ºù„ÅÑ„Åß„Åç„Çã„Åß„Åó„Çá„ÅÜ„ÅãÔºü`,
    `„Åî‰æùÈ†º„ÅÆÂÜÖÂÆπ„ÇíÁêÜËß£„Åó„Åæ„Åó„Åü„ÄÇ‰ª•‰∏ã„ÅÆË¶≥ÁÇπ„Åã„ÇâÊ§úË®é„Åó„Å¶„Åø„Åæ„Åó„Çá„ÅÜÔºö\n\n‚Ä¢ ÊäÄË°ìÁöÑË¶Å‰ª∂\n‚Ä¢ ÂÆüË£ÖÊñπÊ≥ï\n‚Ä¢ ÂøÖË¶Å„Å™„É™„ÇΩ„Éº„Çπ\n\nËøΩÂä†„ÅÆÊÉÖÂ†±„Åå„ÅÇ„Çå„Å∞Êïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`,
    `„Äå${message}„Äç„Å´Èñ¢„Åó„Å¶„ÄÅMARIA CODE„Åß„Çµ„Éù„Éº„Éà„Åß„Åç„ÇãÊñπÊ≥ï„ÇíËÄÉ„Åà„Å¶„ÅÑ„Åæ„Åô„ÄÇ\n\nÈñ¢ÈÄ£„Åô„Çã„ÉÑ„Éº„É´„ÇÑÊ©üËÉΩÔºö\n‚Ä¢ „Ç≥„Éº„ÉâÁîüÊàê\n‚Ä¢ „Éï„Ç°„Ç§„É´Êìç‰Ωú\n‚Ä¢ „ÉÜ„Çπ„Éà‰ΩúÊàê\n‚Ä¢ „Éá„Éó„É≠„Ç§„É°„É≥„Éà\n\n„Å©„Å°„Çâ„ÅÆÊñπÂêë„ÅßÈÄ≤„ÇÅ„Åü„ÅÑ„Åß„Åó„Çá„ÅÜ„ÅãÔºü`,
  ];

  const randomIndex = Math.floor(Math.random() * responses.length);
  return responses[randomIndex] ?? 'Áî≥„ÅóË®≥„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇÁèæÂú®Âá¶ÁêÜ„Åß„Åç„Åæ„Åõ„Çì„ÄÇ';
}

function showHelp() {
  console.log(chalk.cyan.bold('\nüìö Available Commands:'));
  console.log(chalk.yellow('  help     ') + '- Show this help message');
  console.log(chalk.yellow('  history  ') + '- Show conversation history');
  console.log(chalk.yellow('  clear    ') + '- Clear chat history');
  console.log(chalk.yellow('  exit     ') + '- End chat session');
  console.log(chalk.gray('\nüí° Tips:'));
  console.log(chalk.gray('  - Use natural language to describe what you want'));
  console.log(chalk.gray('  - Be specific about files, functions, or features'));
  console.log(chalk.gray('  - Ask for explanations, code creation, or fixes'));
  console.log('');
}

function showHistory(session: ChatSession) {
  console.log(chalk.cyan.bold('\nüìù Conversation History:'));
  if (session.messages.length === 0) {
    console.log(chalk.gray('No messages yet.'));
  } else {
    session.messages.forEach((msg) => {
      const time = msg.timestamp.toLocaleTimeString();
      const role = msg.role === 'user' ? chalk.blue('You') : chalk.green('ü§ñ MARIA');
      console.log(chalk.gray(`[${time}]`), role + ':', msg.content.substring(0, 100) + (msg.content.length > 100 ? '...' : ''));
    });
  }
  console.log('');
}

function showSessionStats(session: ChatSession) {
  const duration = Date.now() - session.startTime.getTime();
  const minutes = Math.floor(duration / 60000);
  const seconds = Math.floor((duration % 60000) / 1000);
  
  console.log(chalk.cyan.bold('\nüìä Session Summary:'));
  console.log(chalk.gray(`Session ID: ${session.sessionId}`));
  console.log(chalk.gray(`Duration: ${minutes}m ${seconds}s`));
  console.log(chalk.gray(`Messages: ${session.messages.length}`));
  console.log(chalk.gray(`Started: ${session.startTime.toLocaleString()}`));
  console.log('');
}

function generateSessionId(): string {
  return 'session_' + Date.now().toString(36) + Math.random().toString(36).substr(2);
}